.Pokemon__form{
    /* width: 40%; */
    border: 1px solid grey;
    border-radius: 5px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    box-shadow: 1px 1px 32px -2px rgba(0,0,0,0.6);
    margin-bottom: 40px;
}
.Pokemon__input{
    outline: none;
    border: 2px solid orangered;
    border-radius: 5px;
    width: 100%;
    padding: 5px;
    margin-bottom: 15px;
}
.Pokemon__btn{
    background-color: orangered;
    border-radius: 25px;
    padding: 7px 12px;
    color: #f1f1f1;
    font-size: 20px;
    font-weight: bold;
    display: flex;
    align-items: center;
}
.Pokemon__btn:hover{
    opacity: 0.8;
}
.Icon-search{
    margin-right: 5px;
}

.spinner{
    padding: 20px;
    color: #010101;
    font-size: 20px;
    font-weight: bold;
    display: flex;
    align-items: center;
}
.icon-spin{
    margin-right: 10px;
    width: 50px;
    height: 50px;
    animation: 3s linear infinite rotate;
}
@keyframes rotate {
    from {
      transform: rotate(0deg);
    }
  
    to {
      transform: rotate(360deg);
    }
  }


  .PokemonCommon__container{
    /* width: 55%; */
    border: 1px solid gray;
    border-radius: 7px;
    padding: 20px;
  }
  .PokemonCommon__list{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
  }
  .PokemonCommon__item{
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
    font-size: 20px;
    font-weight: 500;
    color: #616161;
    text-align: center;
  }
  .PokemonCommon__img{
    display: block;
    width: 100%;
    height: auto;
    margin-bottom: 7px;
  }